<template>
  <v-container fluid class="ma-3">
    <v-row align="center">
      <v-col cols="auto">
        <h2 class="page-title"></h2>
      </v-col>
      <v-spacer />
      <v-col cols="auto" class="mr-4">
        <!-- <v-btn rounded color="primary" to="/products/create">
          <v-icon>mdi-plus</v-icon>
          New
        </v-btn> -->
        <v-dialog v-model="createDialog" persistent>
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" rounded v-bind="attrs" v-on="on">
              <v-icon>mdi-plus</v-icon>
              New
            </v-btn>
          </template>
          <v-card>
            <v-card-title class="headline"></v-card-title>
            <v-card-text>
              <product-create ref="product" />
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" text @click="createDialog = false"
                >Cancel</v-btn
              >
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-col>
    </v-row>

    <v-row>
      <v-col class="auto" v-for="p in products" :key="p.id">
        <v-hover v-slot:default="{ hover }">
          <v-card
            elevation="hover?16:2"
            :class="{ 'on-hover': hover }"
            max-width="200px"
          >
          <router-link :to="`/product/${p.id}`">
            <v-img :src="p.image" width="200" height="200" />
          </router-link>
            
            <v-card-text>
              <p>{{ p.price }}</p>

              <router-link :to="`/product/${p.id}`">
                  <strong>{{ p.title }}</strong>
              </router-link>

              <v-row>
                <v-spacer />
                <v-btn icon color="transparent" :class="{ 'show-btns': hover }">
                  <v-icon> mdi-circle-edit-outline </v-icon>
                </v-btn>
                <v-btn 
                icon 
                color="transparent" 
                :class="{ 'show-btns': hover }"
                @click="showDeleteDialog(p.id)"
                >
                  <v-icon> mdi-close-circle-outline </v-icon>
                </v-btn>
              </v-row>
            </v-card-text>
          </v-card>
        </v-hover>
      </v-col>
    </v-row>
    <v-row align="center">
      <v-pagination
        v-model="page"
        :length="totalPages"
        :total-visible="totalVisible"
        circle
        @input="pageChange"
      ></v-pagination>
      <v-select
        class="change-page-size"
        v-model="pageSize"
        :items="pageItems"
        label="page Size"
        @change="changePageSize"
      ></v-select>
    </v-row>
  
  <v-dialog
      v-model="deleteDialog"
      max-width="500"
    >
      <v-card>
        <v-card-title class="headline"></v-card-title>

        <v-card-text>
          
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            text
            @click="deleteDialog = false"
          >
            Disagree
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            @click="deleteById"
          >
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </v-container>
</template>

<script>
import product from "@/api/service/product.js"; // 

export default {
  name: "Products",
  components: {
    //  
    ProductCreate: () => import("@/views/products/Create.vue"),
  },
  data: () => ({
    products: [], // 
    createDialog: false, // 
    deleteDialog: false, // 
    deleteId: '',        // ID
    page: 1, // 
    pageSize: 10, // 
    totalPages: 1, // 
    totalVisible: 10, // 
    pageItems: [10, 20, 30, 50], // 
  }),
  mounted() {
    this.getDatas();
  },

  methods: {
    getDatas() {
      this.products = [];
      product
        .getProductCount()
        .then((count) => {
          this.totalPages = Math.ceil(count / this.pageSize); //   
        })
        .catch((error) => {});
      product
        .getProducts((this.page - 1) * this.pageSize, this.pageSize) //
        .then((resp) => {
          resp.forEach((item) => {
            this.products.push({
              id: item.id,
              title: item.get("title"), // item.get('')
              price: item.get("price"),
              description: item.get("description"),
              image: item.get("image").get("url"),
              owner: item.get("owner") ? item.get("owner").get("username") : "",
            });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    save() {
      this.$refs.product
        .save()
        .then((item) => {
          this.getDatas();
          this.createDialog = false;
        })
        .catch((error) => {
          this.createDialog = false;
        });
    },
    pageChange(value) {
      console.log(value);
      this.page = value;
      this.getDatas();
    },

    changePageSize(value) {
      this.pageSize = value;
      this.page = 1;
      this.getDatas();
    },

    deleteById(){
      if(this.deleteId){
        product.deleteProductById(this.deleteId)
        .then(resp=>{
          this.getDatas();
        })
        .catch(error=>{});
        this.deleteId = '';  //ID
      }
      this.deleteDialog = false; //
    },

    showDeleteDialog(id){
      this.deleteId = id;    // ID
      this.deleteDialog = true;  // 
    },
  },
};
</script>

<style scoped>
.change-page-size {
  max-width: 70px;
  margin-left: 20px;
}

.show-btns {
  color: rgba(255, 0, 0, 1) !important;
}

.v-card a{
  text-decoration: none;
}
</style>