import LC from 'leancloud-storage';

/**
 * @description: 
 * @param {*} skip : -1*
 * @param {*} limit  
 * @return {*}
 */
let getProducts = (skip = 0, limit = 10) => {
    return new Promise((resolve, reject) => {
        let query = new LC.Query('Product');   // 
        query.include('owner', 'image')        // include Class
            .descending('createdAt')
            .skip(skip)                        // 
            .limit(limit)                      // 
            .find()                            //  find()  
            .then(products => {
                console.log(products);
                resolve(products);             //  
            })
            .catch(error => {
                console.log(error);
                reject(error);                 // 
            });
    });
};

let getProductCount = () => {
    return new Promise((resolve, reject) => {
        let query = new LC.Query('Product');   // 
        query.count()
            .then(count => {
                console.log("Total Count:", count);
                resolve(count);
            })
            .catch(error => {
                console.log(error);
                reject(error);
            });
    });
};

let create = (title, price = 0.0, image = '', description = '') => {
    return new Promise((resolve, reject) => {
        //  Product leancloud
        let Product = LC.Object.extend('Product');
        let product = new Product();   //  Product 
        let currentUser = LC.User.current();   // 
        let imageName = image.name;            // 
        let imageFile = new LC.File(imageName, image);   // 

        // 
        product.set('title', title);
        product.set('price', price);
        product.set('description', description);
        product.set('owner', currentUser);
        product.set('image', imageFile);

        //
        product.save().then(prod => {
            console.log('', prod);
            resolve(prod);   // 
        }, error => {
            console.log('', error);
            reject(error);   // 
        }).catch(error => {
            console.log('', error);
            reject(error);   // 
        });
    });

};

let deleteProductById=(id)=>{
    let prod = LC.Object.createWithoutData('Product', id);
    return new Promise((resolve, reject)=>{
        prod.destroy()
        .then(resp=>{
            console.log('deleteProductById: '. resp);
            resolve(resp);
        })
        .catch(error=>{
            console.log("deleteProductById: ",error);
            reject(error);
        });
    });
};

let getProductById =(id)=>{
    let query = new LC.Query('Product');   // 
    return new Promise((resolve, reject)=>{
        query.include('owner','image')
        .get(id)                           // id
        .then(prod=>{
            console.log('getProductById',prod)
            resolve(prod);                 // 
        })
        .catch(error=>{
            console.log('getProductById'.error)
            resolve(error);
        });
    })
};

// 
export default {
    getProducts,
    create,
    getProductCount,
    deleteProductById,
    getProductById,
}